@import "../../global.scss";
.page{
    all: initial;
    all: unset;
   
}
.resume2{

    background-color: $mainColor;
     margin-bottom: 20px;

    .buttonsection{
        margin-top: 30px;
        display: flex;
        justify-content: center;
        color: $mainColor;
        background-color: solid rgb(255, 208, 0);
        border-radius: 10px;
        margin-bottom: 10px;
    
        button{
            justify-content: center;
            color: $mainColor;
            font-weight: 500;
            border-radius: 5px;
            padding: 0.25rem 0.98rem !important;
            background:  rgb(255, 208, 0);
            transition-duration: 0.4s;
            }

            :hover{
                background: white;
                border-color: rgb(255, 208, 0);
                
            }
        a{
            justify-content: center;
            color: $mainColor;
            font-weight: 500;
            border-radius: 5px;
            padding: 0.25rem 0.98rem !important;
            background:  rgb(255, 208, 0);
            transition-duration: 0.4s;
            text-decoration: none;
        }

    }
    
    
    .resume{
        
        display: flex;
        align-items: column;
        margin-left: 50px;
        margin-right: 50px;
        

        @include mobile{
            justify-content: space-around;
            flex-direction: column;
            align-items: center;
            
          }


        .left{
            flex: 0.5;
            position: relative;
            margin-top: 20px;
            margin-left: 50px;

            .container{
                color: white;

                font-weight: 700;
                font-size: 30px;
                margin-top: 20px;
                margin-bottom: 0px;
                

                .subsection{
                    border-left: 2px solid rgb(255, 208, 0);

                    //.ball{
                    //    display: inline-block;
                    //    margin-bottom: 20px;
                    //    margin-left: -2px;
                    //    content: "";
                    //    width: 16px;
                    //    height: 16px;
                    //    border-radius: 50px;
                    //    left: -0px;
                    //    top: 0;
                    //    background: #fff;
                    //    border: 2px solid #ffd000;
                    //}

                    .title{
                        font-weight: 600;
                        font-size: 15px;
                        margin-top: 5px;
                        margin-bottom: 5px;
                        margin-left: 20px;
                        color: $mainColor;
                        background-color: rgb(255, 208, 0);
                        line-height: 18px;
                        display: inline-block;
                        padding: 0.45rem 0.98rem !important;
                        padding: 12px 20px;
                        border-radius: 6px;
                    }


        
        
                    .date{
                        font-weight: 400;
                        font-size: 17px;
                        font-style: italic;
                        margin-left: 10px;
                    }
        
        
                    ol{
                        font-size: 15px;
                        padding: 10px 10px;
                        font-weight: 500;
                        margin-left: 20px;
        
                        li{
                            margin-top: 10px;
                        }
                    }

                }


            }

            }

        

        .right{
            flex: 0.5;
            position: relative;
            margin-top: 20px;
            margin-left: 50px;

            .container{
                color: white;

                font-weight: 700;
                font-size: 30px;
                margin-top: 20px;
                margin-bottom: 20px;
                

                .subsection{
                    border-left: 2px solid rgb(255, 208, 0);
                    margin-top: -2px;
                    position: relative;
                    

                    .title{
                        font-weight: 600;
                        font-size: 15px;
                        margin-top: 5px;
                        margin-bottom: 5px;
                        margin-left: 10px;
                        color: $mainColor;
                        background-color: rgb(255, 208, 0);
                        line-height: 18px;
                        display: inline-block;
                        padding: 0.45rem 0.98rem !important;
                        padding: 12px 20px;
                        border-radius: 6px;
                    }

        
        
                    .date{
                        font-weight: 400;
                        font-size: 17px;
                        font-style: italic;
                        margin-left: 10px;
                    }
        
        
                    ol{
                        font-size: 15px;
                        padding: 10px 10px;
                        font-weight: 500;
                        margin-left: 20px;
        
                        li{
                            margin-top: 10px;
                        }
                    }

                }


            }

        }
    }

}






