@import "../../global.scss";

.intro {
  background-color: $mainColor;
  display: flex;

  @include mobile{
    flex-direction: column;
    align-items: center;
  }

  .left {
    margin-top: 10px;
    flex: 0.5;
    overflow: hidden;

    @include mobile{
      align-items: flex-start;
      visibility: hidden;
      display: none;
    }

    .imgContainer {
      margin-top: 0px;
      width: 680px;
      height: 680px;
      background-color: white;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      float: right;


      img {
        margin-bottom: 0px;
        width: 100%;
 

        

      }
    }
  }
  .right {
    flex: 0.5;
    position: relative;

    @include mobile{
      padding-left: 0;
      align-items: center;
      position: absolute;

    }

    .wrapper {
      color: white;
      width: 100%;
      height: 75vh;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile{
        padding-left: 0;
        align-items: center;
        margin-top: 90px;
      }

      h1 {
        font-size: 60px;
        margin: 10px 0px;

        @include mobile{
          font-size: 30px;
          padding-bottom: 10px;
        }
      }
      h2 {
        font-size: 30px;
        padding-bottom: 10px;
      }
      h3 {
        font-size: 30px;

        @include mobile{
          font-size: 20px;
          padding-bottom: 10px;
        }

        span {
          font-size: inherit;
          color: rgb(255, 217, 0);
        }

        .ityped-cursor {
          animation: blink 1s infinite;
        }

        @keyframes blink {
            50%{
                opacity: 1;
            }
            100%{
                opacity: 0;
            }
        }
      }

      p{
        margin-left: 10px;
        justify-content: center;
        width: 90%;
        height: fit-content;

        @include mobile{
        
        }

      }

      h4{
          width: 90%;
          margin-left: 10px;
          justify-content: center;
          font-weight: 300;
          font-size: 9;
          

          @include mobile{
            font-size: 20px;
          }

          b{
            font-weight: 700;
          }

      }
    }
    a {
      position: absolute;
      
      bottom: 10px;
      left: 40%;

      @include mobile{
       flex-direction: column;
       bottom: -15px;
       justify-content: center;
      }

      img {
        transform: rotate(90deg);
        width: 60px;
        animation: arrowBlink 2s infinite;

        @include mobile{
          width: 60px;

         }
      }

      @keyframes arrowBlink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}