@import "../../global.scss";

.contact {
  background-color: $mainColor;
  display: flex;

  @include mobile {
    flex-direction: column;
  }

  .left {
    flex: 1;
    overflow: hidden;
    background-color: rgb(255, 217, 0);;

    img {
      height: 100%;
    }
  }
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;

    h2 {
      font-size: 50px;
    }

    form {
      width: 70%;
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      input {
        width: 300px;
        height: 30px;
        font-size: 14px;
        background-color: white;
        border-radius: 10px;

        @include mobile {
          width: 300px;
          height: 30px;
        }
        
      }



      textarea {
        width: 500px;
        height: 200px;
        font-size: 14px;
        border-radius: 10px;

        @include mobile {
          width: 300px;
          height: 150px;
        }
      }

      button {
        width: 150px;
        height: 30px;
        color: $mainColor;
        background-color: rgb(255, 217, 0);
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;

        &:focus {
          outline: none;
        }
      }

      span {
        color: rgb(255, 217, 0);;
      }
    }
  }
}
