@import "./global.scss";

.app{
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);// - 70px);
        
        background-color: white;
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        //scroll-snap-type: y mandatory;
        overflow-y: scroll;
        scrollbar-width: none; //for firefox
        &::-webkit-scrollbar{
            display: none;
        }
        

        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;

            @include mobile{
                height: calc(100vh - 70px);
            }
        }
    }
}